.MovieForm{
    display: -ms-flexbox;
    display: flex;
    margin: 2px 30px 20px 20px;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 23%;
    height: 500px;
}
.MovieForm__field{
    width: 100%;
    border: 4px solid #5f71d6;
}
.movie__gender{
    padding: 5px;
}
.MovieForm *{
    margin-top: 10px;
}
.MovieForm__input{
    width: 90%;
}
.MovieForm__lable{
    margin-top: 10px;
    font-weight: bold;
}
.MovieForm__txt{
    height: 200px;
    width: 90%;
}
.MovieForm__btn{
    width: 70px;
}
.MainBlock{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}



